.error-wrap{
  display: flex;
  margin: 50px auto;
  width: $web_width;
  justify-content: space-between;
  h1{
    @include font-size(2.4);
  }
  h2{
    @include font-size(1.5);
  }
  ul{
    list-style-type: none;
    margin-left: 0;
    padding-left: 0;
    a{
      text-transform: none;
    }
  }
  .page-content{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
}